<template>
    <div style="padding: 20px;">
        <el-button @click.prevent.stop="guide" class="guide" type="primary">新手引导</el-button>
    </div>
</template>

<script>
import Driver from 'driver.js';
import 'driver.js/dist/driver.min.css';
import { ref } from 'vue';
import step from "./step"
export default {
    setup () {
        const driver = ref(null)
        // 实例化driver对象
        driver.value = new Driver()
        const guide = ()=>{
            driver.value.defineSteps(step)
            driver.value.start()
        }

        return {
            guide
        }
    }
}
</script>

